"use client";

import React, { useEffect, useMemo, useRef, useState } from "react";
import {
  createEmptyAddress,
  createEmptyEmployment,
  defaultIntakeData,
  loadIntake,
  saveIntake,
  type AddressEntry,
  type EmploymentEntry,
  type IntakeData,
  type MonthValue,
} from "@/app/lib/intakeStorage";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Textarea } from "@/components/ui/textarea";
import { StepShell } from "./StepShell";

const MONTHS: Array<{ value: MonthValue; label: string }> = [
  { value: "", label: "Month" },
  { value: "01", label: "Jan" },
  { value: "02", label: "Feb" },
  { value: "03", label: "Mar" },
  { value: "04", label: "Apr" },
  { value: "05", label: "May" },
  { value: "06", label: "Jun" },
  { value: "07", label: "Jul" },
  { value: "08", label: "Aug" },
  { value: "09", label: "Sep" },
  { value: "10", label: "Oct" },
  { value: "11", label: "Nov" },
  { value: "12", label: "Dec" },
];

const getYearOptions = () => {
  const current = new Date().getFullYear();
  const years: string[] = [""];
  for (let i = 0; i < 12; i += 1) {
    years.push(String(current - i));
  }
  return years;
};

const STEP_LABELS = [
  "Basics",
  "Contact",
  "Address history",
  "Employment history",
  "Review",
];

export function IntakeFlow() {
  const [stepIndex, setStepIndex] = useState(0);
  const [data, setData] = useState<IntakeData>(() => defaultIntakeData());
  const saveTimer = useRef<number | null>(null);

  useEffect(() => {
    setData(loadIntake());
  }, []);

  useEffect(() => {
    if (saveTimer.current) {
      window.clearTimeout(saveTimer.current);
    }
    saveTimer.current = window.setTimeout(() => {
      saveIntake(data);
    }, 400);

    return () => {
      if (saveTimer.current) {
        window.clearTimeout(saveTimer.current);
      }
    };
  }, [data]);

  const yearOptions = useMemo(() => getYearOptions(), []);

  const goNext = () => {
    setStepIndex((prev) => Math.min(prev + 1, STEP_LABELS.length - 1));
  };
  const goBack = () => {
    setStepIndex((prev) => Math.max(prev - 1, 0));
  };

  const updateAddress = (id: string, patch: Partial<AddressEntry>) => {
    setData((prev) => ({
      ...prev,
      addresses: prev.addresses.map((entry) =>
        entry.id === id ? { ...entry, ...patch } : entry
      ),
    }));
  };

  const updateEmployment = (id: string, patch: Partial<EmploymentEntry>) => {
    setData((prev) => ({
      ...prev,
      employment: prev.employment.map((entry) =>
        entry.id === id ? { ...entry, ...patch } : entry
      ),
    }));
  };

  const addAddress = () =>
    setData((prev) => ({
      ...prev,
      addresses: [...prev.addresses, createEmptyAddress()],
    }));

  const addEmployment = () =>
    setData((prev) => ({
      ...prev,
      employment: [...prev.employment, createEmptyEmployment()],
    }));

  const removeAddress = (id: string) =>
    setData((prev) => ({
      ...prev,
      addresses: prev.addresses.filter((entry) => entry.id !== id),
    }));

  const removeEmployment = (id: string) =>
    setData((prev) => ({
      ...prev,
      employment: prev.employment.filter((entry) => entry.id !== id),
    }));

  return (
    <div className="flex flex-col gap-6">
      <div className="flex flex-wrap items-center gap-3 text-sm text-zinc-500">
        {STEP_LABELS.map((label, idx) => (
          <div
            key={label}
            className={`rounded-full px-3 py-1 ${
              idx === stepIndex
                ? "bg-zinc-900 text-white dark:bg-zinc-100 dark:text-zinc-900"
                : "bg-zinc-100 text-zinc-600 dark:bg-zinc-800 dark:text-zinc-300"
            }`}
          >
            {label}
          </div>
        ))}
      </div>

      {stepIndex === 0 ? (
        <StepShell
          title="Let’s start with the basics"
          description="We’ll keep this short and friendly. You can edit anything later."
        >
          <div className="grid gap-4 sm:grid-cols-2">
            <label className="flex flex-col gap-2 text-sm">
              <Label>Relationship to beneficiary</Label>
              <select
                value={data.basics.relationship}
                onChange={(event) =>
                  setData((prev) => ({
                    ...prev,
                    basics: {
                      ...prev.basics,
                      relationship: event.target.value as IntakeData["basics"]["relationship"],
                    },
                  }))
                }
                className="h-10 rounded-lg border border-zinc-300 bg-white px-3 text-sm text-zinc-900 shadow-sm focus:border-zinc-400 focus:outline-none dark:border-zinc-700 dark:bg-zinc-900 dark:text-zinc-100"
              >
                <option value="spouse">Spouse</option>
                <option value="parent">Parent</option>
                <option value="child">Child</option>
                <option value="sibling">Brother/Sister</option>
              </select>
            </label>
            <div className="rounded-lg border border-dashed border-zinc-300 p-3 text-xs text-zinc-500 dark:border-zinc-700">
              We’ll use this to shape which I-130 questions you see.
            </div>
          </div>

          <div className="grid gap-4 sm:grid-cols-3">
            <label className="flex flex-col gap-2 text-sm">
              <Label>Given name</Label>
              <Input
                value={data.basics.petitioner.givenName}
                onChange={(event) =>
                  setData((prev) => ({
                    ...prev,
                    basics: {
                      ...prev.basics,
                      petitioner: {
                        ...prev.basics.petitioner,
                        givenName: event.target.value,
                      },
                    },
                  }))
                }
                placeholder="Alex"
              />
            </label>
            <label className="flex flex-col gap-2 text-sm">
              <Label>Middle name</Label>
              <Input
                value={data.basics.petitioner.middleName}
                onChange={(event) =>
                  setData((prev) => ({
                    ...prev,
                    basics: {
                      ...prev.basics,
                      petitioner: {
                        ...prev.basics.petitioner,
                        middleName: event.target.value,
                      },
                    },
                  }))
                }
                placeholder="Optional"
              />
            </label>
            <label className="flex flex-col gap-2 text-sm">
              <Label>Family name</Label>
              <Input
                value={data.basics.petitioner.familyName}
                onChange={(event) =>
                  setData((prev) => ({
                    ...prev,
                    basics: {
                      ...prev.basics,
                      petitioner: {
                        ...prev.basics.petitioner,
                        familyName: event.target.value,
                      },
                    },
                  }))
                }
                placeholder="Doe"
              />
            </label>
          </div>

          <div className="grid gap-4 sm:grid-cols-2">
            <label className="flex flex-col gap-2 text-sm">
              <Label>Birth month</Label>
              <select
                value={data.basics.dateOfBirth.month}
                onChange={(event) =>
                  setData((prev) => ({
                    ...prev,
                    basics: {
                      ...prev.basics,
                      dateOfBirth: {
                        ...prev.basics.dateOfBirth,
                        month: event.target.value as MonthValue,
                      },
                    },
                  }))
                }
                className="h-10 rounded-lg border border-zinc-300 bg-white px-3 text-sm text-zinc-900 shadow-sm focus:border-zinc-400 focus:outline-none dark:border-zinc-700 dark:bg-zinc-900 dark:text-zinc-100"
              >
                {MONTHS.map((month) => (
                  <option key={month.value} value={month.value}>
                    {month.label}
                  </option>
                ))}
              </select>
            </label>
            <label className="flex flex-col gap-2 text-sm">
              <Label>Birth year</Label>
              <Input
                value={data.basics.dateOfBirth.year}
                onChange={(event) =>
                  setData((prev) => ({
                    ...prev,
                    basics: {
                      ...prev.basics,
                      dateOfBirth: {
                        ...prev.basics.dateOfBirth,
                        year: event.target.value,
                      },
                    },
                  }))
                }
                placeholder="1990"
              />
            </label>
          </div>
        </StepShell>
      ) : null}

      {stepIndex === 1 ? (
        <StepShell
          title="How can we reach you?"
          description="We only ask for what’s needed for USCIS forms."
        >
          <div className="grid gap-4 sm:grid-cols-2">
            <label className="flex flex-col gap-2 text-sm">
              <Label>Email address</Label>
              <Input
                type="email"
                value={data.contact.email}
                onChange={(event) =>
                  setData((prev) => ({
                    ...prev,
                    contact: { ...prev.contact, email: event.target.value },
                  }))
                }
                placeholder="you@email.com"
              />
            </label>
            <label className="flex flex-col gap-2 text-sm">
              <Label>Phone number</Label>
              <Input
                value={data.contact.phone}
                onChange={(event) =>
                  setData((prev) => ({
                    ...prev,
                    contact: { ...prev.contact, phone: event.target.value },
                  }))
                }
                placeholder="(555) 123-4567"
              />
            </label>
          </div>
        </StepShell>
      ) : null}

      {stepIndex === 2 ? (
        <StepShell
          title="Address history (last 5 years)"
          description="Start with your current address and work backwards. It’s okay to estimate months."
        >
          {data.addresses.map((entry, idx) => (
            <div
              key={entry.id}
              className="rounded-xl border border-zinc-200 bg-zinc-50 p-4 dark:border-zinc-800 dark:bg-zinc-900"
            >
              <div className="flex items-center justify-between">
                <p className="text-sm font-semibold text-zinc-900 dark:text-zinc-100">
                  {idx === 0 ? "Current address" : `Previous address ${idx}`}
                </p>
                {data.addresses.length > 1 ? (
                  <Button
                    type="button"
                    variant="ghost"
                    size="sm"
                    onClick={() => removeAddress(entry.id)}
                  >
                    Remove
                  </Button>
                ) : null}
              </div>

              <div className="mt-4 grid gap-4 sm:grid-cols-2">
                <label className="flex flex-col gap-2 text-sm sm:col-span-2">
                  <Label>Street address</Label>
                  <Input
                    value={entry.street}
                    onChange={(event) =>
                      updateAddress(entry.id, { street: event.target.value })
                    }
                    placeholder="123 Main St"
                  />
                </label>
                <label className="flex flex-col gap-2 text-sm">
                  <Label>Unit / Apt</Label>
                  <Input
                    value={entry.unit}
                    onChange={(event) =>
                      updateAddress(entry.id, { unit: event.target.value })
                    }
                    placeholder="Apt 2B"
                  />
                </label>
                <label className="flex flex-col gap-2 text-sm">
                  <Label>City</Label>
                  <Input
                    value={entry.city}
                    onChange={(event) =>
                      updateAddress(entry.id, { city: event.target.value })
                    }
                    placeholder="San Jose"
                  />
                </label>
                <label className="flex flex-col gap-2 text-sm">
                  <Label>State</Label>
                  <Input
                    value={entry.state}
                    onChange={(event) =>
                      updateAddress(entry.id, { state: event.target.value })
                    }
                    placeholder="CA"
                  />
                </label>
                <label className="flex flex-col gap-2 text-sm">
                  <Label>ZIP / Postal code</Label>
                  <Input
                    value={entry.postal}
                    onChange={(event) =>
                      updateAddress(entry.id, { postal: event.target.value })
                    }
                    placeholder="95112"
                  />
                </label>
                <label className="flex flex-col gap-2 text-sm">
                  <Label>Country</Label>
                  <Input
                    value={entry.country}
                    onChange={(event) =>
                      updateAddress(entry.id, { country: event.target.value })
                    }
                    placeholder="United States"
                  />
                </label>
              </div>

              <div className="mt-4 grid gap-4 sm:grid-cols-4">
                <label className="flex flex-col gap-2 text-sm">
                  <Label>From month</Label>
                  <select
                    value={entry.fromMonth}
                    onChange={(event) =>
                      updateAddress(entry.id, {
                        fromMonth: event.target.value as MonthValue,
                      })
                    }
                    className="h-10 rounded-lg border border-zinc-300 bg-white px-3 text-sm text-zinc-900 shadow-sm focus:border-zinc-400 focus:outline-none dark:border-zinc-700 dark:bg-zinc-900 dark:text-zinc-100"
                  >
                    {MONTHS.map((month) => (
                      <option key={month.value} value={month.value}>
                        {month.label}
                      </option>
                    ))}
                  </select>
                </label>
                <label className="flex flex-col gap-2 text-sm">
                  <Label>From year</Label>
                  <select
                    value={entry.fromYear}
                    onChange={(event) =>
                      updateAddress(entry.id, { fromYear: event.target.value })
                    }
                    className="h-10 rounded-lg border border-zinc-300 bg-white px-3 text-sm text-zinc-900 shadow-sm focus:border-zinc-400 focus:outline-none dark:border-zinc-700 dark:bg-zinc-900 dark:text-zinc-100"
                  >
                    {yearOptions.map((year) => (
                      <option key={year} value={year}>
                        {year || "Year"}
                      </option>
                    ))}
                  </select>
                </label>
                <label className="flex flex-col gap-2 text-sm">
                  <Label>To month</Label>
                  <select
                    value={entry.toMonth}
                    onChange={(event) =>
                      updateAddress(entry.id, {
                        toMonth: event.target.value as MonthValue,
                      })
                    }
                    disabled={entry.isCurrent}
                    className="h-10 rounded-lg border border-zinc-300 bg-white px-3 text-sm text-zinc-900 shadow-sm focus:border-zinc-400 focus:outline-none disabled:opacity-60 dark:border-zinc-700 dark:bg-zinc-900 dark:text-zinc-100"
                  >
                    {MONTHS.map((month) => (
                      <option key={month.value} value={month.value}>
                        {month.label}
                      </option>
                    ))}
                  </select>
                </label>
                <label className="flex flex-col gap-2 text-sm">
                  <Label>To year</Label>
                  <select
                    value={entry.toYear}
                    onChange={(event) =>
                      updateAddress(entry.id, { toYear: event.target.value })
                    }
                    disabled={entry.isCurrent}
                    className="h-10 rounded-lg border border-zinc-300 bg-white px-3 text-sm text-zinc-900 shadow-sm focus:border-zinc-400 focus:outline-none disabled:opacity-60 dark:border-zinc-700 dark:bg-zinc-900 dark:text-zinc-100"
                  >
                    {yearOptions.map((year) => (
                      <option key={year} value={year}>
                        {year || "Year"}
                      </option>
                    ))}
                  </select>
                </label>
              </div>

              <div className="mt-4 flex items-center justify-between">
                <label className="flex items-center gap-2 text-sm">
                  <input
                    type="checkbox"
                    checked={entry.isCurrent}
                    onChange={(event) =>
                      updateAddress(entry.id, {
                        isCurrent: event.target.checked,
                        toMonth: event.target.checked ? "" : entry.toMonth,
                        toYear: event.target.checked ? "" : entry.toYear,
                      })
                    }
                    className="h-4 w-4 rounded border-zinc-300 text-zinc-900"
                  />
                  <span>Current address</span>
                </label>
              </div>
            </div>
          ))}

          <Button type="button" variant="secondary" onClick={addAddress}>
            Add another address
          </Button>
        </StepShell>
      ) : null}

      {stepIndex === 3 ? (
        <StepShell
          title="Employment history (last 5 years)"
          description="Start with your current situation and work backwards."
        >
          {data.employment.map((entry, idx) => (
            <div
              key={entry.id}
              className="rounded-xl border border-zinc-200 bg-zinc-50 p-4 dark:border-zinc-800 dark:bg-zinc-900"
            >
              <div className="flex items-center justify-between">
                <p className="text-sm font-semibold text-zinc-900 dark:text-zinc-100">
                  {idx === 0 ? "Current employment" : `Previous employment ${idx}`}
                </p>
                {data.employment.length > 1 ? (
                  <Button
                    type="button"
                    variant="ghost"
                    size="sm"
                    onClick={() => removeEmployment(entry.id)}
                  >
                    Remove
                  </Button>
                ) : null}
              </div>

              <div className="mt-4 grid gap-4 sm:grid-cols-2">
                <label className="flex flex-col gap-2 text-sm">
                  <Label>Status</Label>
                  <select
                    value={entry.status}
                    onChange={(event) =>
                      updateEmployment(entry.id, {
                        status: event.target.value as EmploymentEntry["status"],
                      })
                    }
                    className="h-10 rounded-lg border border-zinc-300 bg-white px-3 text-sm text-zinc-900 shadow-sm focus:border-zinc-400 focus:outline-none dark:border-zinc-700 dark:bg-zinc-900 dark:text-zinc-100"
                  >
                    <option value="">Select</option>
                    <option value="employed">Employed</option>
                    <option value="unemployed">Unemployed</option>
                    <option value="student">Student</option>
                    <option value="other">Other</option>
                  </select>
                </label>
                <label className="flex flex-col gap-2 text-sm">
                  <Label>Employer name</Label>
                  <Input
                    value={entry.employerName}
                    onChange={(event) =>
                      updateEmployment(entry.id, {
                        employerName: event.target.value,
                      })
                    }
                    placeholder="Company name"
                  />
                </label>
                <label className="flex flex-col gap-2 text-sm">
                  <Label>Job title</Label>
                  <Input
                    value={entry.jobTitle}
                    onChange={(event) =>
                      updateEmployment(entry.id, { jobTitle: event.target.value })
                    }
                    placeholder="Software Engineer"
                  />
                </label>
                <label className="flex flex-col gap-2 text-sm">
                  <Label>City</Label>
                  <Input
                    value={entry.city}
                    onChange={(event) =>
                      updateEmployment(entry.id, { city: event.target.value })
                    }
                    placeholder="San Jose"
                  />
                </label>
                <label className="flex flex-col gap-2 text-sm">
                  <Label>State</Label>
                  <Input
                    value={entry.state}
                    onChange={(event) =>
                      updateEmployment(entry.id, { state: event.target.value })
                    }
                    placeholder="CA"
                  />
                </label>
                <label className="flex flex-col gap-2 text-sm">
                  <Label>Country</Label>
                  <Input
                    value={entry.country}
                    onChange={(event) =>
                      updateEmployment(entry.id, { country: event.target.value })
                    }
                    placeholder="United States"
                  />
                </label>
              </div>

              <div className="mt-4 grid gap-4 sm:grid-cols-4">
                <label className="flex flex-col gap-2 text-sm">
                  <Label>From month</Label>
                  <select
                    value={entry.fromMonth}
                    onChange={(event) =>
                      updateEmployment(entry.id, {
                        fromMonth: event.target.value as MonthValue,
                      })
                    }
                    className="h-10 rounded-lg border border-zinc-300 bg-white px-3 text-sm text-zinc-900 shadow-sm focus:border-zinc-400 focus:outline-none dark:border-zinc-700 dark:bg-zinc-900 dark:text-zinc-100"
                  >
                    {MONTHS.map((month) => (
                      <option key={month.value} value={month.value}>
                        {month.label}
                      </option>
                    ))}
                  </select>
                </label>
                <label className="flex flex-col gap-2 text-sm">
                  <Label>From year</Label>
                  <select
                    value={entry.fromYear}
                    onChange={(event) =>
                      updateEmployment(entry.id, { fromYear: event.target.value })
                    }
                    className="h-10 rounded-lg border border-zinc-300 bg-white px-3 text-sm text-zinc-900 shadow-sm focus:border-zinc-400 focus:outline-none dark:border-zinc-700 dark:bg-zinc-900 dark:text-zinc-100"
                  >
                    {yearOptions.map((year) => (
                      <option key={year} value={year}>
                        {year || "Year"}
                      </option>
                    ))}
                  </select>
                </label>
                <label className="flex flex-col gap-2 text-sm">
                  <Label>To month</Label>
                  <select
                    value={entry.toMonth}
                    onChange={(event) =>
                      updateEmployment(entry.id, {
                        toMonth: event.target.value as MonthValue,
                      })
                    }
                    disabled={entry.isCurrent}
                    className="h-10 rounded-lg border border-zinc-300 bg-white px-3 text-sm text-zinc-900 shadow-sm focus:border-zinc-400 focus:outline-none disabled:opacity-60 dark:border-zinc-700 dark:bg-zinc-900 dark:text-zinc-100"
                  >
                    {MONTHS.map((month) => (
                      <option key={month.value} value={month.value}>
                        {month.label}
                      </option>
                    ))}
                  </select>
                </label>
                <label className="flex flex-col gap-2 text-sm">
                  <Label>To year</Label>
                  <select
                    value={entry.toYear}
                    onChange={(event) =>
                      updateEmployment(entry.id, { toYear: event.target.value })
                    }
                    disabled={entry.isCurrent}
                    className="h-10 rounded-lg border border-zinc-300 bg-white px-3 text-sm text-zinc-900 shadow-sm focus:border-zinc-400 focus:outline-none disabled:opacity-60 dark:border-zinc-700 dark:bg-zinc-900 dark:text-zinc-100"
                  >
                    {yearOptions.map((year) => (
                      <option key={year} value={year}>
                        {year || "Year"}
                      </option>
                    ))}
                  </select>
                </label>
              </div>

              <div className="mt-4 flex items-center justify-between">
                <label className="flex items-center gap-2 text-sm">
                  <input
                    type="checkbox"
                    checked={entry.isCurrent}
                    onChange={(event) =>
                      updateEmployment(entry.id, {
                        isCurrent: event.target.checked,
                        toMonth: event.target.checked ? "" : entry.toMonth,
                        toYear: event.target.checked ? "" : entry.toYear,
                      })
                    }
                    className="h-4 w-4 rounded border-zinc-300 text-zinc-900"
                  />
                  <span>Current situation</span>
                </label>
              </div>
            </div>
          ))}

          <Button type="button" variant="secondary" onClick={addEmployment}>
            Add another job or status
          </Button>
        </StepShell>
      ) : null}

      {stepIndex === 4 ? (
        <StepShell
          title="Quick review"
          description="We’ll highlight anything missing so you can fix it."
        >
          <div className="rounded-lg border border-zinc-200 bg-zinc-50 p-4 text-sm text-zinc-600 dark:border-zinc-800 dark:bg-zinc-900 dark:text-zinc-300">
            <p>
              Addresses entered: <strong>{data.addresses.length}</strong>
            </p>
            <p className="mt-1">
              Employment entries: <strong>{data.employment.length}</strong>
            </p>
            <p className="mt-2">
              We’ll flag timeline gaps and missing details once the mapping is
              connected to the PDF.
            </p>
          </div>
          <label className="flex flex-col gap-2 text-sm">
            <Label>Anything you want to revisit later?</Label>
            <Textarea
              value={data.addresses[0]?.notes || ""}
              onChange={(event) =>
                updateAddress(data.addresses[0].id, {
                  notes: event.target.value,
                })
              }
              placeholder="Optional notes for later..."
            />
          </label>
        </StepShell>
      ) : null}

      <div className="flex items-center justify-between">
        <Button
          type="button"
          variant="ghost"
          onClick={goBack}
          disabled={stepIndex === 0}
        >
          Back
        </Button>
        <div className="flex items-center gap-2">
          {stepIndex < STEP_LABELS.length - 1 ? (
            <Button type="button" onClick={goNext}>
              Continue
            </Button>
          ) : (
            <Button type="button" variant="secondary" onClick={() => saveIntake(data)}>
              Save progress
            </Button>
          )}
        </div>
      </div>
    </div>
  );
}
